.page-header__nav-toggle {
  position: relative;
  display: block;
  margin-top: 21px;
  padding: 0;
  border: none;
  background-color: transparent;
  width: 50px;
  height: 24px;
  margin-right: 20px;
}
.page-header__nav-toggle::before {
  content: "";
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto 0;
  box-shadow: 0 10px 0 0 #ffffff, 0 -10px 0 0 #ffffff;
}
.page-header__nav-toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 10px 0 0 #D22856, 0 -10px 0 0 #D22856;
}
.page-header__nav-toggle:active::before {
  opacity: 0.3;
}
@media (min-width: 660px) {
  .page-header__nav-toggle {
    margin-top: 43px;
  }
}
@media (min-width: 1200px) {
  .page-header__nav-toggle {
    display: none;
  }
}
.page-header__flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 66px;
}
@media (min-width: 660px) {
  .page-header__flex {
    min-height: 66px;
  }
}
@media (min-width: 1200px) {
  .page-header__flex {
    max-width: 940px;
    margin: 0 auto;
    align-items: center;
  }
}
.page__header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.page__header--no-js {
  position: static;
  background-color: #283645;
  box-shadow: inset 0 1px 0 #000000;
}
.page__header--no-js .main-nav {
  display: block;
  position: static;
  border-top: 1px solid #000000;
}
.page__header--no-js .page-header__nav-toggle {
  display: none;
}
@media (min-width: 1200px) {
  .page__header {
    position: absolute;
    background: rgba(0, 0, 0, 0.3);
  }
}
.page__header--nav-opened {
  background-color: #283645;
}
.page__header--nav-opened .page-header__nav-toggle {
  box-shadow: none;
}
.page__header--nav-opened .page-header__nav-toggle:hover::before {
  box-shadow: none;
  background: #D22856;
}
.page__header--nav-opened .page-header__nav-toggle:hover::after {
  background-color: #D22856;
}
.page__header--nav-opened .page-header__nav-toggle:active::before {
  box-shadow: none;
  background-color: #D22856;
}
.page__header--nav-opened .page-header__nav-toggle:active::after {
  background-color: #D22856;
  opacity: 0.3;
}
.page__header--nav-opened .page-header__nav-toggle::before,
.page__header--nav-opened .page-header__nav-toggle::after {
  content: "";
  width: 34px;
  height: 3px;
  position: absolute;
  background-color: #ffffff;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto 0 auto auto;
  box-shadow: none;
}
.page__header--nav-opened .page-header__nav-toggle::before {
  transform: rotate(45deg);
}
.page__header--nav-opened .page-header__nav-toggle::after {
  transform: rotate(-45deg);
}
.page__header--nav-opened .main-nav {
  display: block;
  border-top: 1px solid #000000;
}
