.competition-main__bg-wrapper {
  background-color: @basic-blue;
  background-image: url("../img/bg/header-bg--mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;

  padding-top: 93px;
  padding-bottom: 45px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg/header-bg--mobile@2x.jpg");
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg/header-bg--mobile@2x.jpg");
  }

  @media (min-width: 660px) {
    padding-top: 178px;
    padding-bottom: 58px;
    background-image: url("../img/bg/header-bg--tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bg/header-bg--tablet@2x.jpg");
    }
  }

  @media (min-width: 1200px) {
    padding-top: 170px;
    padding-bottom: 88px;
    background-image: url("../img/bg/header-bg--desktop.jpg");

    @media (min-resolution) {
      background-image: url("../img/bg/header-bg--desktop@2x.jpg");
    }
  }
}

.competition-main__page-slogan {
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  color: @basic-white;
  margin: 0 auto;
  max-width: 280px;
  text-align: center;

  @media (min-width: 660px) {
    font-size: 50px;
    line-height: 68px;
    max-width: 620px;
  }

  @media (min-width: 1200px) {
    max-width: 810px;
  }
}

.competition-main__modal-warning,
.competition-main__modal-done {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;

  &--opened {
    display: block;
  }
}
