.contacts {
  padding-top: 41px;
  padding-bottom: 35px;
  margin: 0 10px;
  max-width: 960px;

  @media (min-width: 660px) {
    padding-top: 44px;
    padding-bottom: 27px;
  }

  @media (min-width: 1000px) {
    margin: 0 auto;
  }

  @media (min-width: 1200px) {
    padding-top: 59px;
    padding-bottom: 20px;
  }
}

.contacts__flex {
  @media (min-width: 660px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 46px;
    padding-right: 30px;
  }

  @media (min-width: 1200px) {
    justify-content: space-between;
  }
}

.contacts__heading {
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
  font-style: normal;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 33px;
  max-width: 230px;

  @media (min-width: 660px) {
    font-size: 40px;
    line-height: 54px;
    max-width: 370px;
  }

  @media (min-width: 1200px) {
    font-size: 50px;
    line-height: 68px;
    margin-bottom: 40px;
    max-width: 470px;
  }
}

.contacts p {
  font-size: 18px;
  line-height: 25px;
  font-weight: bold;
  font-style: normal;
  color: @basic-black;
  margin: 0;
  margin-bottom: 19px;
  text-align: center;
  padding: 0 20px;

  &:last-child {
    margin-bottom: 0;

    @media (min-width: 660px) {
      margin: 0 auto;
    }

    @media (min-width: 870px) {
      margin: 0 10px;
    }
  }

  @media (min-width: 660px) {
    max-width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10px;
    margin-bottom: 45px;
    padding: 0;
  }

  @media (min-width: 1200px) {
    margin-bottom: 0;

    &:nth-child(2) {
      order: 3;
    }
  }
}

.contacts__link {
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  font-style: normal;
  color: @basic-red;
  text-decoration: none;
  display: block;

  &[href]:hover {
    color: @basic-black;
    text-decoration-line: underline;
  }

  &[href]:active {
    color: @basic-blue;
    opacity: 0.3;
  }

  @media (min-width: 1200px) {
    padding-top: 20px;
  }
}
